import React, { useState } from 'react'
import Head from 'next/head'
import Link from 'next/link'
import Header from '../components/Header'
import { Row, Col, List, Icon } from 'antd'
import Author from '../components/Author'
import Advert from '../components/Advert'
import Footer from '../components/Footer'
import Tag from '../components/Tag'
import axios from 'axios'
import request from '../config/api'
import '../static/styles/pages/index.css'

const Home = (list) => {
  const [articleList, setArticleList] = useState(list.data.articles)

  return (
    <>
      <Head>
        <meta charSet="UTF-8"/>
        <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
        <title>白鸟亦悲否？</title>
        <link rel="shortcut icon" href="/favicon.ico" type="image/x-icon"/>
      </Head>
      <Header/>
      <Row className="comm-main" type="flex" justify="center">
        <Col className="comm-left" xs={24} sm={24} md={16} lg={18} xl={14}>
          <List
            header={<div>最新日志</div>}
            itemLayout="vertical"
            dataSource={articleList}
            renderItem={item => (
              <List.Item>
                <div className="list-title">
                  <Link href={{ pathname: '/detail', query: { id: item.id } }}>
                    <a>{item.title}</a>
                  </Link>
                </div>
                <div className="list-icon">
                  <span><Icon type="calendar"/> {item.createTime}</span>
                  <span><Icon type="tags"/> {item.tags[0].name}</span>
                  <span><Icon type="fire"/> {item.view}人</span>
                </div>
                <div className="list-context">{item.introduce}</div>
              </List.Item>
            )}
          />
        </Col>
        <Col className="comm-right" xs={0} sm={0} md={7} lg={5} xl={4}>
          <Author/>
          <Tag/>
          <Advert/>
        </Col>
      </Row>

      <Footer/>
    </>
  )
}

Home.getInitialProps = async () => {
  const promise = new Promise((resolve) => {
    axios(request.fetchArticleList).then(
      (res) => resolve(res.data)
    )
  })

  return await promise
}

export default Home
